import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import './App.css';
import Home from './pages/Home';
import Register from './pages/Register';
import RegistrationForm from './pages/RegistrationForm';
import LogIn from './pages/LogIn';
import CreateColor from './pages/CreateColor';
import ColorDetail from './pages/ColorDetail';
import UserProfile from './pages/UserProfile';
import GroupPage from './pages/GroupPage';
import axios from 'axios';
import { AuthContext } from './context/auth-context';
// import HomePage from './HomePage/HomePage';

function getCookie(cname) {
  var name = cname + "=";
  var decodedCookie = decodeURIComponent(document.cookie);
  var ca = decodedCookie.split(';');
  for(var i = 0; i <ca.length; i++) {
    var c = ca[i];
    while (c.charAt(0) === ' ') {
      c = c.substring(1);
    }
    if (c.indexOf(name) === 0) {
      return c.substring(name.length, c.length);
    }
  }
  return "";
}

function deleteCookie( name ) {
  document.cookie = name + '=; expires=Thu, 01 Jan 1970 00:00:01 GMT;';
}

const App = () => {
  const [auth, setAuth] = useState({ isAuthenticated: false, username: "", signOut: () => {} });
  const [sessionId, setSessionId] = useState("");

  useEffect(() => {
    setSessionId(getCookie("sessionId"));
  }, [auth, sessionId]);

  const signOut = () => {
    deleteCookie("sessionId");
    setSessionId("");
  }
  
  useEffect(() => {
    if (sessionId !== "") {
      axios.get(`${process.env.REACT_APP_BACKEND_API_URL || "https://localhost/api"}/sessions/${sessionId}/user`)
        .then(user => {
          setAuth(auth => ({
            ...auth,
            isAuthenticated: true,
            username: user.data.username,
            signOut: signOut
          }));
        })
        .catch(error => {
          console.log(error);
        });
    } else {
      setAuth(auth => ({
        ...auth,
        isAuthenticated: false,
        username: "",
        signOut: signOut
      }));
    }
  }, [sessionId]);

  return (
    <AuthContext.Provider value={auth}>
      <Router>
        <Switch>
          <Route path="/register/form">
            <RegistrationForm />
          </Route>
          <Route path="/register">
            <Register />
          </Route>
          <Route path="/login">
            <LogIn setAuth={setAuth} />
          </Route>

          <Route path="/user/:username">
            <UserProfile />
          </Route>
          {/* <Route path="/user">
            <AllUsers />
          </Route> */}

          <Route path="/color/create">
            <CreateColor />
          </Route>
          <Route path="/color/:id">
            <ColorDetail />
          </Route>
          {/* <Route path="/color">
            <AllColors />
          </Route> */}

          <Route path="/group/:id">
            <GroupPage />
          </Route>
          {/* <Route path="/group">
            <AllGroups />
          </Route> */}

          <Route path="/">
            <Home />
          </Route>
        </Switch>

        {/* <div className={`App App-${theme}`}>
          <HomePage theme={theme} />
        </div> */}
      </Router>
    </AuthContext.Provider>
  );
}

export default App;
