import React from 'react';
import { Link } from 'react-router-dom';
import './Home.css';
import Header from '../components/Header';
import ColorList from '../components/ColorList';

const Home = () => {
  const HomeBanner = () => {
    return (
      <div class="banner">
        <p>Some message.</p>
        <Link to="/color/create">Create Color</Link>
      </div>
    );
  }

  return (
    <>
      <Header />
      <HomeBanner />
      <ColorList />
    </>
  );
}

export default Home;
