import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';

const LogIn = props => {
  const [formData, setFormData] = useState({
    email: "",
    password: ""
  });

  const handleChange = event => {
    setFormData(
      {
        ...formData,
        [event.target.name]: event.target.value
      }
    );
  }
  
  const handleSubmit = event => {
    event.preventDefault();
    axios.post(`${process.env.REACT_APP_BACKEND_API_URL || "https://localhost/api"}/sessions`, {
        ...formData
      })
      .then(session => {
        const expirationDate = (new Date(session.data.expiration)).toUTCString();
        document.cookie = `sessionId=${session.data._id}; expires=${expirationDate}`;
        props.setAuth({
          isAuthenticated: true,
          username: session.data.user
        });
      })
      .catch(error => {
        console.log(error.response.data);
      });
  }

  return (
    <>
      <Link to="/">Color 257</Link>
      <main>
        <form onSubmit={handleSubmit}>
          <div class="registration-form-input-set">
            <div class="regristration-form-input-group">
              <label class="registration-form-label">Email</label>
              <br />
              <input type="email" name="email" class="registration-form-text-input" value={formData.email} onChange={handleChange} />
            </div>
            <div class="regristration-form-input-group">
              <label class="registration-form-label">Password</label>
              <br />
              <input type="password" name="password" class="registration-form-text-input" value={formData.password} onChange={handleChange} />
            </div>
          </div>
          <input type="submit" value="Log In" />
        </form>
        <hr />
        <Link to="/register">Register</Link>
        <p>Forgot password</p>
      </main>
    </>
  );
}

export default LogIn;
