import React from 'react';
import { Link } from 'react-router-dom';
import './Register.css';

const Register = () => {
  return (
    <>
      <header class="register-header">
        <Link to="/" class="register-h1">Color 257</Link>
        <p class="register-p">Register to start sharing colors!</p>
      </header>
      <main class="register-main">
        <button class="register-google">Google</button>
        <button class="register-facebook">Facebook</button>
        <Link to="/register/form">By email</Link>
        <hr class="register-hr" />
        <Link to="/login">Log In</Link>
      </main>
    </>
  );
}

export default Register;
