import React from 'react';
import Header from '../components/Header';
import Slider from '@material-ui/core/Slider';

const CreateColor = () => {
  const ColorControl = () => {
    return (
      <div className="color-control">
        <div className="color"></div>
        <button className="color-control-reroll">Reroll</button>
      </div>
    );
  }

  const SaveColor = () => {
    return (
      <form>
        <div>
          <label>Color Name</label>
          <br />
          <input type="text" name="name" />
        </div>
        <div>
          <label>Description</label>
          <textarea name="description" />
        </div>
      </form>
    );
  }

  const ColorSettings = () => {
    const HueSlider = () => {
      return (
        <Slider min={0} max={359} value={[20, 339]} />
      );
    }

    const LightnessSlider = () => {
      return (
        <Slider min={0} max={765} value={[255, 510]} />
      );
    }

    const GradientSeparationSlider = () => {
      return (
        <Slider min={0} max={100} value={[25, 75]} />
      );
    }

    return (
      <div className="color-settings">
        <h2 className="color-settings-h2">Settings</h2>
        <div className="color-settings-input-group">
          <label className="color-settings-label">Amount of colors:</label>
          <select name="colorsAmount" className="color-settings-select" defaultValue="1">
            <option value="1" className="color-settings-option">1</option>
            <option value="2">2</option>
          </select>
        </div>
        <div className="color-settings-input-group">
          <label className="color-settings-label">Hue</label>
          <HueSlider />
        </div>
        <div className="color-settings-input-group">
          <label className="color-settings-label">Lightness</label>
          <LightnessSlider />
        </div>
        <div className="color-settings-input-group">
          <label className="color-settings-label">Gradient Separation</label>
          <GradientSeparationSlider />
        </div>
      </div>
    );
  }

  return (
    <>
      <Header />
      <ColorControl />
      <SaveColor />
      <ColorSettings />
    </>
  );
}

export default CreateColor;
