import React from 'react';
import { Link } from 'react-router-dom';


const ColorList = () => {
  const ColorPreview = () => {
    const ColorNameLink = props => {
      return (
        <div class="color-name">
          <Link to={props.to}>
            {props.name}
          </Link>
        </div>
      );
    }

    const ColorUsernameLink = props => {
      return (
        <div class="color-username">
          <Link to={props.to}>
            {props.username}
          </Link>
        </div>
      );
    }

    return (
      <div class="color-container">
        <div class="color"></div>
        <div class="color-text">
          <ColorNameLink to="/color/1" name="Color name" />
          <ColorUsernameLink to="/user/santiago" username="Username" />
        </div>
      </div>
    );
  }

  return (
    <div class="color-list">
      <ColorPreview />
      <ColorPreview />
      <ColorPreview />
      <ColorPreview />
      <ColorPreview />
      <ColorPreview />
      <ColorPreview />
      <ColorPreview />
    </div>
  );
}

export default ColorList;
