import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';
// import './RegisterForm.css';

const RegisterForm = () => {
  const [formData, setFormData] = useState(
    {
      username: "",
      email: "",
      password: ""
    }
  );

  const handleChange = event => {
    setFormData(
      {
        ...formData,
        [event.target.name]: event.target.value
      }
    );
  }
  
  const handleSubmit = event => {
    axios.post(`${process.env.REACT_APP_BACKEND_API_URL || "https://localhost/api"}/users`, {
        ...formData
      })
      .then(response => {
        console.log(response);
      })
      .catch(error => {
        console.log(error);
      });
    event.preventDefault();
  }

  return (
    <>
      <Link to="/">Color 257</Link>
      <main className="registration-form-main">
        <form onSubmit={handleSubmit}>
          <div className="registration-form-input-set">
            <div className="regristration-form-input-group">
              <label className="registration-form-label">Username</label>
              <br />
              <input type="text" name="username" className="registration-form-text-input" value={formData.username} onChange={handleChange} />
            </div>
            <div className="regristration-form-input-group">
              <label className="registration-form-label">Email</label>
              <br />
              <input type="email" name="email" className="registration-form-text-input" value={formData.email} onChange={handleChange} />
            </div>
            <div className="regristration-form-input-group">
              <label className="registration-form-label">Password</label>
              <br />
              <input type="password" name="password" className="registration-form-text-input" value={formData.password} onChange={handleChange} />
            </div>
          </div>
          <p>
            By signing up you agree to both our Terms and Conditions, and our Privacy Policy.
          </p>
          <input type="submit" value="Register" />
        </form>
      </main>
    </>
  );
}

export default RegisterForm;
