import React from 'react';
import { Link } from 'react-router-dom';
import './ColorDetail.css';
import Header from '../components/Header';


const ColorDetail = () => {
  return (
    <div className="color-detail">
      <Header />
      <Color />
      <ColorInfo />
    </div>
  );
}

const Color = () => {
  return (
    <div className="color-detail-color"></div>
  );
}

const ColorInfo = () => {
  return (
    <div className="color-detail-color-info">
      <ColorTextInfo />
      <ColorData />
    </div>
  );
}

const ColorTextInfo = () => {
  return (
    <div className="color-detail-color-text-info">
      <Link to="/user/santiago" className="color-detail-color-text-info-username">
        Username
      </Link>
      <div className="color-detail-color-text-info-name">
        Color Name
      </div>
      <div className="color-detail-color-text-info-description">
        Description
      </div>
    </div>
  );
}

const ColorData = () => {
  return (
    <>
      <div className="color-detail-color-data-group">
        <label className="color-data-color-data-label">Colors</label>
        <div className="color-detail-color-data-color-list">
          <div className="color-detail-color-data-color">
            <span className="color-detail-color-data-color-square"></span>
            <span className="color-detail-color-data-color-hex">#000000</span>
          </div>
          <div className="color-detail-color-data-color">
            <span className="color-detail-color-data-color-square"></span>
            <span className="color-detail-color-data-color-hex">#000000</span>
          </div>
        </div>
      </div>
      <div className="color-detail-color-data-group">
        <label className="color-data-color-data-label">Angle</label>
        <div className="color-detail-color-data-color-number">135deg</div>
      </div>
      <div className="color-detail-color-data-group">
        <label className="color-data-color-data-label">Hue</label>
        <div className="color-detail-color-data-color-number">180</div>
      </div>
      <div className="color-detail-color-data-group">
        <label className="color-data-color-data-label">Lightness</label>
        <div className="color-detail-color-data-color-number">400</div>
      </div>
      <div className="color-detail-color-data-group">
        <label className="color-data-color-data-label">Gradient Separation</label>
        <div className="color-detail-color-data-color-number">50</div>
      </div>
      <button>Share</button>
    </>
  );
}

export default ColorDetail;